<template>
  <div>
    <form ref="uploadForm" id="uploadForm" style="display:inline;">
      <input
        type="file"
        id="fileId"
        ref="addPicFile"
        name="file"
        accept="image/jpg,image/jpeg,image/png,image/bmp"
        style="display:none;"
        @change="addPic"
      />
    </form>
    <div class="showImg" v-for="(item, index) in photoList" :key="item.src">
      <img
        class="addimg-items"
        v-show="photoList.length > 0"
        :src="item.src"
        @mouseenter="enter(item)"
        @mouseleave="leave(item)"
      />
      <div
        class="demo-upload-list-cover"
        v-show="item.imgHover"
        @mouseenter="enter(item)"
        @mouseleave="leave(item)"
      >
        <Icon type="ios-eye" @click="showImg(item.src)" />
        <Icon type="ios-trash" @click="reset(index)" />
      </div>
    </div>
    <img
      class="addimg-items addBtn"
      v-show="photoList.length < 5"
      src="../assets/add.png"
      @click="$refs.addPicFile.click()"
    />
    <!-- <Modal title="图片详情" v-model="visible">
      <img :src="show.photo" v-if="visible" style="width: 100%" />
      <div slot="footer"></div>
    </Modal> -->
  </div>
</template>

<script>
import common from "../assets/common";
import Http from "../assets/http";
export default {
  components: {},
  props: ["photo"],
  data() {
    return {
      visible: false,
      show: {
        photo: ""
      },
      photoList: [],
      list: []
    };
  },
  methods: {
    //移入
    enter(item) {
      item.imgHover = true;
    },
    //移出
    leave(item) {
      item.imgHover = false;
    },
    //查看大图
    showImg(src) {
      this.visible = true;
      this.show.photo = src;
    },
    //添加图片
    addPic() {
      let file = common.upload(this.$refs.addPicFile);
      let reader = new FileReader();
      reader.onload = e => {
        let img = new Image();
        img.src = e.target.result;
        img.onload = () => {
          let imgsrc = common.compress(img);
          this.$options.methods.uploadPic.bind(this)(imgsrc);
          this.$refs.addPicFile.value = "";
        };
      };
      reader.readAsDataURL(file);
    },
    uploadPic(data) {
      let res = Http.getAxios("POST", `/image/upload`, data);
      res.then(data => {
        this.list.push({
          src: data.message,
          imgHover: false
        });
        this.$emit("addPic", this.list);
      });
    },
    reset(index) {
      this.list.splice(index, 1);
      this.$emit("resetPic", this.list);
    },
    // 清空图片
    clearPic() {
      this.photoList = [];
      this.$emit("clearPic", this.photoList);
    }
  },
  mounted() {
    // 因为修改调用查询方法返回过来的数据会慢一点，所以延迟赋值，否则图片将不会展示
    setTimeout(() => {
      this.photoList = this.photo;
      this.list = this.photo;
    }, 300);
  }
};
</script>

<style scoped>
.showImg {
  position: relative;
  float: left;
  margin-right: 10px;
}
.demo-upload-list-cover {
    width: 100px;
    height: 100px;
    padding: 33px 20px;
}
.demo-upload-list-cover i {
  font-size: 30px;
}
.addBtn {
  float: left;
  margin-right: 10px;
  background-color: #383842;
  padding: 1rem;
}
</style>